<template>
  <div class="page">
    <van-nav-bar title="首页" :border="false" />
    <ul>
      <router-link
        v-for="item in router_link"
        :key="item.name"
        :to="{ name: item.name }"
        tag="li"
        active-class="active"
        >{{ item.title }}</router-link
      >
    </ul>
    <img
      src="../assets/images/annual_membership_privilege.png"
      class="annual_membership_privilege"
      alt="年度会员特权"
    />
    <section>
      <router-view></router-view>
    </section>
    <router-link :to="{ name: 'my' }" tag="div">
      <img src="../assets/images/my.png" alt="我的" class="my"
    /></router-link>
    <img
      src="../assets/images/customer_service.png"
      alt="客服"
      class="customer_service"
    />
  </div>
</template>

<script>
export default {
  data () {
    return {
      router_link: [
        {
          name: 'self-operated-goods',
          title: '自营商品'
        },
        {
          name: 'group-buy-doctor',
          title: '团购医生'
        },
        {
          name: 'super-package',
          title: '超级礼包'
        }
      ]
    }
  }
}
</script>

<style lang="less" scoped>
.page {
  .van-nav-bar {
    width: 100%;
    height: 44px;
    background-color: #ff093c;
    .van-nav-bar__title {
      font-size: 36px; /*px*/
      line-height: 44px;
      font-weight: bold;
      color: #ffffff;
    }
  }
  > ul {
    display: flex;
    flex-direction: row;
    height: 36px;
    padding: 0 10px;
    background-color: #ff093c;
    font-size: 26px; /*px*/
    line-height: 36px;
    color: #ffffff;
    li {
      margin-right: 28px;
    }
    .active {
      font-size: 30px; /*px*/
      font-weight: bold;
    }
  }
  .annual_membership_privilege {
    width: 100%;
  }
  section {
    position: relative;
    min-height: 360px;
    padding: 20px 10px 0;
    margin-top: -32px;
    border-top-left-radius: 30px; /*no*/
    border-top-right-radius: 30px; /*no*/
    background-color: #f6f6f6;
  }
  .my {
    position: fixed;
    right: 0;
    bottom: 286px;
  }
  .customer_service {
    position: fixed;
    right: 0;
    bottom: 188px;
  }
}
</style>



